body {
  height: 90vw;
  background-color: #c3f3da;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.search-block {
  float: left;
  width: 70vw;
  align-self: auto;
  display: flex;
}

.loader {
  color: gray;
  align-self: center;
  margin-left: .5vw;
  font-size: max(1rem, min(20px, 2rem));
}

#search-box {
  width: max(min(200px, 9vw), min(300px, max(600px, 30vw)));
  height: max(1.5em, min(4vw, 1.5em));
  border: 2px solid #c3f3da;
  border-radius: 8px;
  align-self: center;
  padding-left: 10px;
  font-size: max(1rem, min(4vw, 1.5rem));
}

#search-result {
  flex-direction: column;
  list-style-type: none;
  display: flex;
}

.country-info {
  flex-direction: column;
  margin-top: 6px;
  display: flex;
}

#search-result > li {
  flex-direction: column;
  margin: 3px;
  display: flex;
}

.country-general {
  grid-gap: 5px;
  flex-direction: row;
  grid-template-columns: minmax(min-content, 98px) auto;
  justify-content: start;
  align-self: auto;
  display: grid;
}

.country-flag {
  columns: 1;
  max-height: 49px;
  align-self: center;
  justify-self: center;
  display: grid;
}

.country-name {
  align-self: center;
  padding-left: 1vw;
  font-size: max(2.4rem, min(3vw, 2.75rem));
  font-weight: bold;
}

.country-details {
  grid-template-rows: repeat(3, max-content);
  grid-template-columns: max-content;
  justify-content: start;
  display: grid;
}

.country-details > div {
  grid-template-columns: minmax(140px, 10vw) auto;
  justify-content: start;
  display: grid;
}

.country-details-category {
  grid-column: 1;
  align-content: center;
  margin: .25vw;
  font-size: max(1.5rem, min(1.5vw, 2rem));
  font-weight: bold;
  display: grid;
}

.country-datails-value {
  grid-column: 2;
  align-content: center;
  margin: .25vw;
  font-size: max(1.5rem, min(1.5vw, 2rem));
  display: grid;
}

